:root{
  --box-spacing: 8px;
}

/* Container */
.container{
}

/* Spaces */
.m0 { margin: 0!important; }
.ms { margin-left: var(--box-spacing)!important; }
.me { margin-right: var(--box-spacing)!important; }
.mt { margin-top: var(--box-spacing)!important; }
.mb { margin-bottom: var(--box-spacing)!important; }

/* Flex */
.flex {display: flex;}
.row {flex-flow: row;}
.column {flex-flow: column;}
.center {justify-content: center; align-items: center;}
.center-j {justify-content: center;}
.center-a {align-items: center;}

/* Box */
.box{
  background-color: #fff;
  margin: var(--box-spacing);
  padding: 10px;
  border-radius: 10px;
}

.logo{
  height: 90px;
  padding: 5px 0;
}

/* Tools */
.icon{
  height: 30px;
  width: auto;
}

.media.mobile{
  display: none;
}

@media screen and (max-width: 6in) {
  .nomobile{
    display: none;
  }
  .mobile{
    display: unset;
  }
}