/* Land */
.land{
  display: flex;
  flex-flow: row;
  justify-content: stretch;
  align-items: stretch;
  gap: var(--box-spacing);
  margin: var(--box-spacing);
}
.land-typo{
  flex-basis: 40%;
}
.land-display{
  flex-basis: 60%;
  background-image: url('../img/cube.png');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 80vh;
}



@media screen and (max-width: 6in) {
  .land{
    flex-flow: column-reverse;
  }
  .land-typo{
    min-height: 30vh;
  }
  .land-display{
    min-height: 50vh;
  }
}